.text-block {
  padding: 3.7rem 1.7rem;
  box-shadow: 0 3px 10px rgba($c-brand-light-2, 1);
  z-index: 2;
  border-radius: 10px;
  position: relative;
  margin: 0 0.8rem;

  img {
    border-radius: 10px;
  }

  @include media-breakpoint-up(md) {
    margin: 0;
  }

  @include media-breakpoint-up(xl) {
    padding: 5.6rem 4.8rem;
  }

  &--left {
    @include media-breakpoint-up(xl) {
      padding-right: 8rem;
    }
  }

  &--right {
    @include media-breakpoint-up(xl) {
      padding-right: 8rem;
    }
  }

  &--blockquote {
    box-shadow: none;
    margin: 0;
    padding: 0;

    @include media-breakpoint-up(md) {
      margin-top: -5.8rem;
    }

    @include media-breakpoint-up(xl) {
      padding: 3.7rem;
      margin-top: 0;
    }
  }

  &__title-intro {
    margin-bottom: 0.8rem;
  }

  &__paragraph {
    margin: 0 0 2.4rem;

    @include media-breakpoint-up(md) {
      margin: 0 0 3.1rem;
    }
  }

  &__logo {
    position: relative;
    left: 50%;
    transform: translateX(-50%);

    @include media-breakpoint-up(md) {
      position: absolute;
      left: auto;
      right: 0;
      bottom: 5rem;
      width: 35%;
      transform: translateX(0);
    }
  }

  &__blockquote {
    margin-top: 3.5rem;
    position: relative;

    @include media-breakpoint-up(md) {
      margin-top: 0;
      padding-left: 3rem;
      margin-left: 3rem;
    }

    @include media-breakpoint-up(xl) {
      padding-left: 3rem;
      margin-left: 10rem;
    }

    &::after {
      @include pseudo(block, absolute, "\F10E");
      font-family: FontAwesome;
      opacity: 0.15;
      font-size: 8.3rem;
      top: 0.5rem;
      right: 0;

      @include media-breakpoint-up(md) {
        right: auto;
        opacity: 0.5;
        font-size: 2.4rem;
        transform: translateX(-100%);
        left: 0;
        top: 0;
      }
    }
  }
}