section {
    padding: 60px 15px;

    @include media-breakpoint-up(md) {
        padding: 70px 0;

    }
}

.section {
    &__description {
        max-width: 900px;
        margin-bottom: 40px;
    }

    &__header {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}