@include font-face("FontAwesome", "../fonts/FontAwesome/fontawesome-webfont");

@font-face {
  font-family: Antonio;
  src: url("../fonts/Antonio/Antonio-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Antonio;
  src: url("../fonts/Antonio/Antonio-Light.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Antonio;
  src: url("../fonts/Antonio/Antonio-Bold.ttf");
  font-weight: 700;
}

body,
address {
  font-family: $font-text;
  font-weight: 300;
  color: $black;
  font-size: 1.4rem;
  font-style: normal;
  line-height: 2.2rem;

  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

a {
  font-weight: 700;
  color: $c-brand;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5 {
  font-family: $font-heading;
  font-weight: 700;
  text-transform: uppercase;
}

.font-accent {
  font-family: $font-accent;
}

.heading-1 {
  font-size: 3.4rem;
  line-height: 3.4rem;

  @media (min-width: 365px) {
    font-size: 3.8rem;
    line-height: 3.8rem;
  }

  @include media-breakpoint-up(md) {
    font-size: 6.4rem;
    line-height: 6.4rem;
  }
}

.heading-2 {
  font-size: 3.2rem;
  line-height: 3.8rem;

  @include media-breakpoint-up(md) {
    font-size: 4.8rem;
    line-height: 5.6rem;
  }
}

.heading-3 {
  font-size: 2.4rem;
  line-height: 2.8rem;

  @include media-breakpoint-up(md) {
    font-size: 3.2rem;
    line-height: 3.8rem;
  }
}

.heading-4 {
  font-size: 1.5rem;
  line-height: 2rem;
  letter-spacing: 2px;

  @include media-breakpoint-up(md) {
    font-size: 1.8rem;
    line-height: 2rem;
  }
}

.heading-5 {
  font-size: 1.5rem;
  line-height: 1.6rem;

  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 1.8rem;
  }
}

.paragraph,
p {
  font-size: 1.7rem;
  line-height: 2.6rem;

  @include media-breakpoint-up(md) {
    font-size: 2rem;
    line-height: 1.7;
  }
}

.btn-main {
  font-weight: 700;
  letter-spacing: .5px;
  font-size: 1.5rem;
  line-height: 1.8rem;

  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 1.9rem;
  }
}

.navigation__link {
  color: $c-black;
  font-size: 2.2rem;
  font-weight: 500;
  text-decoration: none;

  @include media-breakpoint-up(md) {
    font-size: 3rem;
  }

  @include media-breakpoint-up(xl) {
    font-size: 1.5rem;
    line-height: 1.8rem;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.section-title-small {
  z-index: 5;
  color: $c-black;
  margin-bottom: 2rem;
  display: block;
  text-transform: none;
  font-weight: 300;
  position: relative;
  display: inline-block;
  font-family: $font-accent;

  &::after {
    content: "";
    z-index: -1;
    left: 0;
    position: absolute;
    bottom: 4px;
    width: 120%;
    height: 4px;
    background-image: linear-gradient(to right, rgba($c-brand, .8), $c-white);
    transform-origin: center;
    display: block;
    border-radius: 10px;
  }

  &--no-rotate {
    margin-bottom: 3rem;

    &::after {
      transform: rotate(0);
    }
  }
}

.hero {
  &__title {
    font-size: 3.0rem;
    line-height: 3.5rem;

    @media (min-width: 365px) {
      font-size: 3.8rem;
      line-height: 3.8rem;
    }

    @include media-breakpoint-up(md) {
      font-size: 6.4rem;
      line-height: 6.4rem;
    }
  }

  &__paragraph {
    font-size: 1.7rem;
    line-height: 2.7rem;

    @include media-breakpoint-up(md) {
      font-size: 2rem;
      line-height: 1.7;
    }
  }
}