.contact {
  &__group {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
  }

  &__link {
    margin-bottom: 1rem;
    text-align: center;
  }

  &__links {
    margin-bottom: 3.5rem;
  }

  &__logo {
    display: block;
    margin-left: auto;
  }

  &__navigation {
    &-list {
      list-style-type: none;
      padding-left: 0;
    }

    &-item {
      &::after {
        display: none;
      }
    }

    &-link {
      color: $c-black;
      display: inline-block;
      margin-bottom: 0.6rem;

      &:hover {
        color: $c-brand;
      }
    }
  }
}