.hero {
  background-image: linear-gradient(to top, $c-white, transparent), url("../img/hero-background.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;

  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 30%;

  min-height: 100vh;

  @include media-breakpoint-up(md) {
    padding: 23% 0 12%;
  }

  @include media-breakpoint-up(xl) {
    padding: 13% 0 5%;
  }

  &__slider {
    position: relative;
  }

  &__slide {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  &__slider-arrow {
    outline: none;
    display: block;
    top: 50%;
    width: 35px;
    height: 35px;
    border-radius: 3px;
    border: 3px solid $c-brand;
    background-color: transparent;
    border-bottom-color: transparent !important;
    border-left-color: transparent !important;
    position: absolute;
    z-index: 10;

    &.swiper-button-disabled {
      opacity: 0.5;
      border-color: $c-lightgray-2;
    }

    &--prev {
      left: 20px;
      transform: rotate(-135deg);
    }

    &--next {
      right: 20px;
      transform: rotate(-135deg) scale(-1);
    }
  }

  &__buttons {
    a {
      margin-bottom: 2rem;

      @include media-breakpoint-up(sm) {
        margin-bottom: 0;
      }
    }
  }

  &__title {
    margin: 0;
    text-align: center;

    .bg-white {
      padding: 0.8rem 2rem;
    }
  }

  &__title-intro {
    display: block;
    margin-bottom: 0.8rem;
  }

  &__paragraph {
    max-width: 60%;
    margin: 2.1rem auto 2.4rem;
    font-weight: 400;

    @include media-breakpoint-up(md) {
      margin: 2.9rem 0 3.6rem;
      max-width: 80rem;
    }

    @include media-breakpoint-up(xl) {
      margin: 3.1rem 0 5.4rem;
    }
  }

  &__scroll-down-btn {
    margin: 2rem auto;

    @include media-breakpoint-up(md) {
      margin-top: 4.1rem;
    }

    @include media-breakpoint-up(xl) {
      margin-top: 5.4rem;
    }
  }
}