// COLORS
$c-primary: rgb(57, 57, 57);
$c-brand: #e14f86;
$c-brand-light: #ea807d;
$c-brand-light-2: #f9e4e6;
$c-accent: #80c3ba;
$c-white: #fff;
$c-black: #000;
$c-gray: #00000080;
$c-lightgray: #f5f5f5; // lightgray
$c-lightgray-2: #707070; // lightgray
$c-warning: #fb4416;

$font-text: 'Roboto Slab',
sans-serif;
$font-heading: 'Montserrat',
serif;
$font-accent: 'Allura',
serif;

// TRANSITIONS
$transition-transform-default: transform 0.2s ease-in-out;
$transition-opacity-default: opacity 0.2s ease-in-out;
$transition-all-default: all 0.2s ease-in-out;

// SETTINGS
$desktop-breakpoint: xl;
$element-offset-md: 4%;
$element-offset-xl: 4.5%;